//****************************
// LATEST ISSUE
//****************************
.front-page-latest {
  background: $cover-ochre;
  padding: 4rem 0;
  article {
    text-align: center;
    h1 {
      color: $grey;
      padding: 0 3rem;
      @include fontmake($size: 4, $margin: 2, $family: "Aleo", $weight: 700);
      text-transform: none; }
    p {
      color: $base;
      @include fontmake($size: 1.6, $family: "Raleway", $weight: 700, $margin: 3);
      text-align: center; }
    a {
      display: inline-block;
      color: $cover-ochre;
      text-transform: uppercase;
      background: $grey;
      padding: 1.6rem 3rem;
      @include fontmake($size: 1.2, $family: "Raleway", $weight: 400, $margin: 0);
      border-radius: 40px;
      transition: 0.3s all ease;
      &:hover {
        background: transparentize($grey, 0.3); } } } }

//****************************
// FRONT PAGE QUEUE
//****************************

.front-page-queue {
  article {
    @include span(3 of 12);
    height: 650px;
    position: relative;
    margin-bottom: 2rem;
    .entry-image {
      img {
        max-width: 100%;
        min-width: 100%;
        height: auto;
        border-radius: 50%;
        transition: 0.3s all ease-in-out;
        box-sizing: border-box;
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
        box-shadow: 0 0 0 2px $green, 0 0 0 10px $beige;
        &:hover {
          box-shadow: 0 0 0 12px $green; } } }
    header {
      // position: absolute
      // top: 100px
      text-align: center;
      .entry-title {
        @include fontmake($size: 1.4rem, $family: "Aleo", $margin: 0.4);
        a {
          @include textback($pull: 10px, $color: $grey); } }
      p.author {
        @include fontmake($size: 0.8, $family: "Raleway", $weight: 400, $margin: 0.4);
        text-align: center; }
      time {
        display: block;
        width: 50px;
        height: 50px;
        margin: 0 auto;
        padding-top: 16px;
        background-color: $base;
        color: $green;
        border-radius: 50%;
        @include fontmake($size: 0.7, $family: "Raleway", $weight: 700, $margin: 0); } } } }
