article.error {
  position: relative;
  header {
    text-align: center;
    @include span(9 of 12 no-gutters);
    @include pre(1.5 of 12);
    margin-bottom: 6rem;
    h1 {
      color: $purple;
      @include fontmake($size: 3.4, $line: 3.4, $family: "Aleo", $weight: 700, $margin: 2); } }

  .alert-warning {
    @include span(6 of 12 no-gutters);
    @include pre(3 of 12);
    margin-bottom: 2rem;
    text-align: center; }

  .search-form {
    @include span(6 of 12 no-gutters);
    @include pre(3 of 12);
    margin-bottom: 6rem;
    position: relative;

    span.screen-reader-text {
      display: none; }

    input.search-field {
      width: 100%;
      @include fontmake($size: 1, $family: "Raleway", $weight: 400, $margin: 0);
      color: $purple;
      padding: 1rem 1.4rem;
      margin: 1rem 0;
      border: 0;
      outline: 0;
      border-radius: 28px;
      box-shadow: 0 0 0 2px $green, 0 0 0 10px $beige;
      transition: 0.3s all ease;
      &:active {
        border: 0;
        outline: 0; }
      &:focus {
        box-shadow: 0 0 0 12px $green; } }

    .search-submit {
      width: 60px;
      height: 60px;
      position: absolute;
      top: 14px;
      right: -1px;
      padding: 0;
      background: white;
      @include fontmake($family: "Raleway", $size: 0.8, $weight: 400, $margin: 0);
      border: 2px solid $green;
      border-radius: 30px;
      transition: 0.3s all ease;
      &:hover {
        cursor: pointer;
        border: 2px solid $beige; } }

    .mce_inline_error {
      background-color: transparent;
      color: $red; } } }
